body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ant-input-search input,
.ant-list-bordered {
  border-radius: 0px;
}

.ant-list-item-meta {
  padding-left: 1.4rem;
}

.ant-list-item-content {
  padding-right: 6px;
}

.App {
  text-align: center;
}
body {
  height: 100vh;
}

#root,
.App-header {
  /* min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; */
  overflow: hidden;
  color: white;
  height: 100%;
}

.App-link {
  color: #61dafb;
}

.player-container {
  width: 100%;
  background: #fff;
}

.list-item-song {
  -webkit-transition: color 0.3s linear;
  transition: color 0.3s linear;
}

.list-item-song:hover .song-name {
  -webkit-transition: color 0.3s linear;
  transition: color 0.3s linear;
  color: #4286f4;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.music-player-container {
  background: #282828;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  padding-top: 0.5rem;
}

.active-song {
  padding: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 25%;
}

.center-panel {
  width: 50%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  display: -webkit-flex;
  display: flex;
}

.functional-buttons {
  width: 75%;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
}

.functional-buttons > button {
  margin: 1rem 1rem 0rem 1rem;
}

.song-progress {
  width: 80%;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  padding-bottom: 1rem;
}

.song-volume {
  width: 25%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding: 1rem;
}

.player-expander {
  position: absolute;
  top: -1rem;
  margin-left: auto;
  margin-right: auto;
  background: #47494c;
  border-radius: 25%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

